import React from 'react';

import Header from './Header.js';
import Profile from './Profile.js';
import EditProfilePopup from './EditProfilePopup.js';
import EditAvatarPopup from './EditAvatarPopup.js';
import AddPlacePopup from './AddPlacePopup.js';
import ImagePopup from './ImagePopup.js';
import mestoApi from '../utils/api';
import { CurrentUserContext } from '../contexts/CurrentUserContext';

function App() {

  const [isEditProfilePopupOpen, setIsEditProfilePopupOpen] = React.useState(false);
  const [isAddPlacePopupOpen, setIsAddPlacePopupOpen] = React.useState(false);
  const [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] = React.useState(false);
  const [selectedCard, setSelectedCard] = React.useState({});
  const [currentUser, set–°urrentUser] = React.useState({});
  const [cards, setCards] = React.useState([]);

  function handleEditAvatarClick() {
    setIsEditAvatarPopupOpen(!isEditAvatarPopupOpen);
  }

  function handleEditProfileClick() {
    setIsEditProfilePopupOpen(!isEditProfilePopupOpen);
  }

  function handleAddPlaceClick() {
    setIsAddPlacePopupOpen(!isAddPlacePopupOpen);
  }

  function handleCardClick(card) {
    const newObj = { ...card }
    setSelectedCard(newObj);
  }

  function handleUpdateUser(user) {
    mestoApi.patchUserProfile(user.name, user.about)
      .then((user) => { set–°urrentUser(user); closeAllPopups(); })
      .catch((err) => { console.log(`–û—à–∏–±–∫–∞: ${err}`); })
  }

  function handleUpdateAvatar(user) {
    mestoApi.patchUserAvatar(user.avatar)
      .then((user) => { set–°urrentUser(user); closeAllPopups(); })
      .catch((err) => { console.log(`–û—à–∏–±–∫–∞: ${err}`); })
  }

  function handleAddPlaceSubmit(card) {
    mestoApi.postCard(card.name, card.link)
      .then((card) => { setCards([...cards, card]); closeAllPopups(); })
      .catch((err) => { console.log(`–û—à–∏–±–∫–∞: ${err}`); })
  }

  function closeAllPopups() {
    setIsAddPlacePopupOpen(false);
    setIsEditProfilePopupOpen(false);
    setIsEditAvatarPopupOpen(false);
    setSelectedCard({});
  }

  function getUserInfo() {
    mestoApi.getUserProfile()
      .then((result) => { const newObj = { ...result }; set–°urrentUser(newObj); })
      .catch((err) => { console.log(`–û—à–∏–±–∫–∞: ${err}`); })
  }

  function getCards() {
    mestoApi.getInitialCards()
      .then((res) => { const arr = res.slice(); setCards(arr) })
      .catch((err) => { console.log(`–û—à–∏–±–∫–∞: ${err}`); })
  }

  function handleCardLike(card) {
    // –°–Ω–æ–≤–∞ –ø—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —É–∂–µ –ª–∞–π–∫ –Ω–∞ —ç—Ç–æ–π –∫–∞—Ä—Ç–æ—á–∫–µ
    const isLiked = card.likes.some(i => i._id === currentUser._id);
    // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–ø—Ä–æ—Å –≤ API –∏ –ø–æ–ª—É—á–∞–µ–º –æ–±–Ω–æ–≤–ª—ë–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –∫–∞—Ä—Ç–æ—á–∫–∏
    mestoApi.likeCard(card._id, isLiked)
      .then((newCard) => {
        // –§–æ—Ä–º–∏—Ä—É–µ–º –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ –Ω–∞ –æ—Å–Ω–æ–≤–µ –∏–º–µ—é—â–µ–≥–æ—Å—è, –ø–æ–¥—Å—Ç–∞–≤–ª—è—è –≤ –Ω–µ–≥–æ –Ω–æ–≤—É—é –∫–∞—Ä—Ç–æ—á–∫—É
        const newCards = cards.map((c) => c._id === card._id ? newCard : c);
        // –û–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–µ–π—Ç
        setCards(newCards);
      })
      .catch((err) => {
        console.log(`–û—à–∏–±–∫–∞: ${err}`);
      })
  }
  function handleCardDelete(card) {
    const isOwn = card.owner._id === currentUser._id;
    if (isOwn) {
      mestoApi.deleteCard(card._id)
        .then(() => {
          const newCards = cards.filter((c) => c._id !== card._id);
          setCards(newCards);
        })
        .catch((err) => {
          console.log(`–û—à–∏–±–∫–∞: ${err}`);
        })
    }
  }

  React.useEffect(() => {
    getUserInfo();
    getCards();
  }, [])

  return (
    <>
      <CurrentUserContext.Provider value={currentUser}>
        <div className="root">
          <Header />
          <Profile
            onAddPlace={handleAddPlaceClick}
            onEditAvatar={handleEditAvatarClick}
            onEditProfile={handleEditProfileClick}
            onCardClick={handleCardClick}
            cards={cards}
            onCardLike={handleCardLike}
            onCardDelete={handleCardDelete} />
          <div className="places-list root__section">
          </div>
        </div>
        <AddPlacePopup onAddPlace={handleAddPlaceSubmit} isOpen={isAddPlacePopupOpen} onClose={closeAllPopups} />
        <EditProfilePopup onUpdateUser={handleUpdateUser} isOpen={isEditProfilePopupOpen} onClose={closeAllPopups} />
        <EditAvatarPopup onUpdateAvatar={handleUpdateAvatar} isOpen={isEditAvatarPopupOpen} onClose={closeAllPopups} />
        <ImagePopup card={selectedCard} onClose={closeAllPopups} />
      </CurrentUserContext.Provider>
    </>
  );
}

export default App;
